.emptyCart{
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
    padding: 10vmax;
}
.emptyCart > svg{
    font-size:5vmax;
    color: tomato;
}
.emptyCart > p{
    font-size: 2vmax;
}
.emptyCart > a{
    background-color: rgb(51,51,51);
    color: white;
    border: none;
    padding: 1vmax 3vmax;
    cursor: pointer;
    font: 400 1vmax "Roboto";
    text-decoration: none;
}



.cartPage{
    padding: 5vmax;
}

.cartHeader{
    background-color: tomato;
    width: 90%;
    margin: auto;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    font: 300 0.7vmax "Roboto";
}
.cartHeader > p{
    margin: 10px;
}
.cartHeader > p:last-child{
    text-align: end;
}
.cartContainer{
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    /* align-items: flex-end; */
}
.cartInput{
    height: 8vmax;
    align-items: center;
    height: 8vmax;
    position: relative;
    top: 30%;
}
.cartInput > button{
    border: none;
    background-color: rgba(0,0,0,0.616);
    padding: 0.5vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
}
.cartInput button:hover{
    background-color: rgba(0,0,0,0.767);
}

.cartInput > input{
    border: none;
    padding: 0.5vmax;
    width: 1vmax;
    text-align: center;
    outline: none;
    font: 400 0.8vmax "Roboto";
    color: rgba(0,0,0,0.74);
}
.cartSubtotal{
    display: flex;
    padding: 0.5vmax;
    justify-content: flex-end;
    color: rgba(0,0,0,0.753);
    font: 300 1vmax cursive;
    box-sizing: border-box;
    align-items: center;
    height: 8vmax;
}

.cartGrossProfit{
    display: grid;
    grid-template-columns: 2fr 1.31fr;
}
.cartGrossProfitBox{
    border-top: 3px solid tomato;
    margin: 1vmax 4vmax;
    box-sizing: border-box;
    justify-content: space-between;
    display: flex;
    padding: 2vmax 0;
    font: 300 1vmax "Roboto";
}
.checkOutBtn{
    display: flex;
    justify-content: flex-end;
}
.checkOutBtn > button{
    background-color: tomato;
    color: white;
    border: none;
    padding: 0.8vmax 4vmax;
    width: 50%;
    font: 300 0.8vmax "Roboto";
    margin: 1vmax 4vmax;
    cursor: pointer;
    border-radius: 30px;

}
@media screen and(max-width:600px) {
    .cartPage{
        padding: 0;
        min-height: 60vh;

    }
    .cartHeader{
        width: 100%;
        font: 300 1.7vmax "Roboto";
        grid-template-columns: 3fr 1fr 1fr;
    }
    .cartContainer{
        width: 100%;
        grid-template-columns: 3fr 1fr 1fr;
    }
    .cartInput{
        height: 20vmax;
    }
    .cartInput > button{
        padding: 1.5vmax;
    }
    .cartInput > input{
        width: 2vmax;
        padding: 1.5vmax;
        font: 400 1.8vmax "Roboto";
    }
    .cartSubtotal{
        padding: 1.5vmax;
        height: 20vmax;
        font: 300 2vmax "Roboto";
    }
    .cartGrossProfit{
        display: grid;
        grid-template-columns: 0fr 2fr;
    }
    .cartGrossProfitBox{
        padding: 2vmax;
        font: 300 2vmax "Roboto";
    }
    .checkOutBtn >button{
        padding: 2vmax 4vmax;
        width: 100%;
        font: 300 2vmax "Roboto";
    }
}

